@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto+Condensed:wght@300;400;700&display=swap');

*{
  font-family: 'Roboto Condensed', Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Header */
.titulo h2{
  color: #fffdf7;
  font-family: 'Pacifico', cursive;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  
}

.titulo{
  background: #6441A5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2a0845, #6441A5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2a0845, #6441A5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: 20px 0px;
  background-image: url(media/Fondo1.png);
  background-position: center;
}

.container{
  margin: 30px auto;
  width: 80%;
}

.inputCategory{
  background-color: rgb(255, 255, 255);
  padding: 4px 10px;
  border: 3px solid transparent;
  color: #0a2342;
  display: block;
  font-size: 1rem;
  margin: 0px auto;
  margin-top: 10px;
  width: 40%;
  transition: all .3s ease;

}
.inputCategory:focus{
  border-color: #f46197;
}


/* GifGrid */
.categoryName{
  padding: 5px 10px;
  border-bottom: 1.5px solid #ccc;
  font-size: 1.5rem;
  font-weight: 600;
  color: #0a2342;
}
.gifsContainer{
  display: flex;
  flex-wrap: wrap;
}


/* GifGridItem.js */
.gifCard{
  align-content: center;
  border: none;
  overflow: hidden;
  border-radius: 3px;
  margin: 10px 0;
  margin-right: 10px;
  box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.2);
  max-width: 300px;
  background: #f46197;
}
.gifCard .gifTitle{
  
  text-align: center;
  padding: 10px 10px;
  font-size: 1rem;
  background: #f46197;
  color: #fffdf7;
}

.gifCard .gif{
  min-height: 130px;
  max-height: 130px;
  width: 100%;
  vertical-align: top;
}


